"use client";

import { useQuery } from "convex/react";
import { api } from "../../convex/_generated/api";
import Navigation from "@/components/Navigation";

export default function DashboardPage() {
  const contacts = useQuery(api.contacts.list);
  const campaigns = useQuery(api.campaigns.list);

  const stats = {
    totalContacts: contacts?.length ?? 0,
    activeContacts: contacts?.filter((c: { status: string }) => c.status === "active").length ?? 0,
    totalCampaigns: campaigns?.length ?? 0,
    runningCampaigns: campaigns?.filter((c: { status: string }) => c.status === "running").length ?? 0,
  };

  const recentCampaigns = campaigns?.slice(0, 5) ?? [];
  const listHealth = stats.totalContacts > 0 ? Math.round((stats.activeContacts / stats.totalContacts) * 100) : 0;

  return (
    <Navigation>
      <div>
        {/* Header */}
        <div style={{ marginBottom: 32 }}>
          <h1 className="page-title">Dashboard</h1>
          <p className="page-subtitle">Monitor your email campaigns and subscriber health</p>
        </div>

        {/* Stats Grid */}
        <div className="stats-grid">
          <div className="stat-card">
            <div className="stat-label">Total Contacts</div>
            <div className="stat-value">{stats.totalContacts.toLocaleString()}</div>
          </div>
          <div className="stat-card">
            <div className="stat-label">Active Subscribers</div>
            <div className="stat-value">{stats.activeContacts.toLocaleString()}</div>
          </div>
          <div className="stat-card">
            <div className="stat-label">Total Campaigns</div>
            <div className="stat-value">{stats.totalCampaigns.toLocaleString()}</div>
          </div>
          <div className="stat-card">
            <div className="stat-label">Running Now</div>
            <div className="stat-value" style={{ color: stats.runningCampaigns > 0 ? "var(--accent-success)" : undefined }}>
              {stats.runningCampaigns}
            </div>
          </div>
        </div>

        {/* Two Column Layout */}
        <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gap: 24 }}>
          {/* List Health */}
          <div className="card">
            <div className="card-header">
              <h3 className="card-title">List Health</h3>
              <span className={`badge ${listHealth >= 80 ? "badge-success" : listHealth >= 50 ? "badge-warning" : "badge-danger"}`}>
                {listHealth}% Active
              </span>
            </div>

            <div style={{ marginBottom: 24 }}>
              <div className="progress-container">
                <div className="progress-bar" style={{ width: `${listHealth}%` }} />
              </div>
            </div>

            <div style={{ display: "grid", gridTemplateColumns: "repeat(3, 1fr)", gap: 16, textAlign: "center" }}>
              <div>
                <div style={{ fontSize: 24, fontWeight: 700, color: "var(--accent-success)" }}>
                  {contacts?.filter((c: { status: string }) => c.status === "active").length ?? 0}
                </div>
                <div style={{ fontSize: 12, color: "var(--text-muted)", marginTop: 4 }}>Active</div>
              </div>
              <div>
                <div style={{ fontSize: 24, fontWeight: 700, color: "var(--accent-warning)" }}>
                  {contacts?.filter((c: { status: string }) => c.status === "unsubscribed").length ?? 0}
                </div>
                <div style={{ fontSize: 12, color: "var(--text-muted)", marginTop: 4 }}>Unsubscribed</div>
              </div>
              <div>
                <div style={{ fontSize: 24, fontWeight: 700, color: "var(--accent-danger)" }}>
                  {contacts?.filter((c: { status: string }) => c.status === "bounced").length ?? 0}
                </div>
                <div style={{ fontSize: 12, color: "var(--text-muted)", marginTop: 4 }}>Bounced</div>
              </div>
            </div>
          </div>

          {/* Quick Actions */}
          <div className="card">
            <div className="card-header">
              <h3 className="card-title">Quick Actions</h3>
            </div>

            <div style={{ display: "grid", gap: 12 }}>
              <a href="/campaigns" className="btn btn-secondary" style={{ justifyContent: "flex-start" }}>
                <span>üìß</span> Create New Campaign
              </a>
              <a href="/templates" className="btn btn-secondary" style={{ justifyContent: "flex-start" }}>
                <span>üìù</span> Design Template
              </a>
              <a href="/contacts" className="btn btn-secondary" style={{ justifyContent: "flex-start" }}>
                <span>üë•</span> Import Contacts
              </a>
              <a href="/senders" className="btn btn-secondary" style={{ justifyContent: "flex-start" }}>
                <span>‚öôÔ∏è</span> Configure SMTP
              </a>
            </div>
          </div>
        </div>

        {/* Recent Campaigns */}
        <div className="card" style={{ marginTop: 24 }}>
          <div className="card-header">
            <h3 className="card-title">Recent Campaigns</h3>
            <a href="/campaigns" className="btn btn-ghost btn-sm">View All ‚Üí</a>
          </div>

          {recentCampaigns.length === 0 ? (
            <div className="empty-state" style={{ padding: 40 }}>
              <div className="empty-icon">üìß</div>
              <h3 className="empty-title">No campaigns yet</h3>
              <p className="empty-text">Create your first campaign to start sending emails</p>
            </div>
          ) : (
            <div className="table-container" style={{ border: "none" }}>
              <table>
                <thead>
                  <tr>
                    <th>Campaign</th>
                    <th>Status</th>
                    <th>Sent</th>
                    <th>Failed</th>
                    <th>Progress</th>
                  </tr>
                </thead>
                <tbody>
                  {recentCampaigns.map((campaign: { _id: string; name: string; status: string; sentCount: number; failedCount: number; totalRecipients: number }) => {
                    const progress = campaign.totalRecipients > 0
                      ? Math.round(((campaign.sentCount + campaign.failedCount) / campaign.totalRecipients) * 100)
                      : 0;
                    return (
                      <tr key={campaign._id}>
                        <td style={{ fontWeight: 500 }}>{campaign.name}</td>
                        <td>
                          <span
                            className={`badge ${campaign.status === "running"
                                ? "badge-success"
                                : campaign.status === "completed"
                                  ? "badge-info"
                                  : campaign.status === "paused"
                                    ? "badge-warning"
                                    : "badge-neutral"
                              }`}
                          >
                            {campaign.status}
                          </span>
                        </td>
                        <td style={{ color: "var(--accent-success)" }}>{campaign.sentCount}</td>
                        <td style={{ color: campaign.failedCount > 0 ? "var(--accent-danger)" : "var(--text-muted)" }}>
                          {campaign.failedCount}
                        </td>
                        <td style={{ width: 150 }}>
                          <div style={{ display: "flex", alignItems: "center", gap: 8 }}>
                            <div className="progress-container" style={{ flex: 1 }}>
                              <div className="progress-bar" style={{ width: `${progress}%` }} />
                            </div>
                            <span style={{ fontSize: 12, color: "var(--text-muted)", minWidth: 36 }}>{progress}%</span>
                          </div>
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>
          )}
        </div>
      </div>
    </Navigation>
  );
}
